body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.img {
  background-image: url(https://cdn.kinsights.com/cache/87/9f/879fa2be63f52cf389964567deadd102.jpg?fbclid=IwAR367-VYlI5aB1QDSEvpmO7QBp1cZsANsHzz4p9tdphFfW4F-3Qg3vv3_Xc);
  background-repeat: no-repeat;
  background-size: cover;
}

.jumbotron.m-width {
  max-width: 800px;
}

textarea {
  resize:none;
}

a.card {
  color: inherit;
}

a.card:hover {
  text-decoration: none;
  color: inherit;
}

.jumbotron.bg-dark {
  background-repeat: no-repeat;
  background-image: url(https://ayudaenaccion.org/ong/wp-content/uploads/2017/06/educacion-en-valores-peliculas.jpg);
  background-size: cover;
  background-blend-mode: soft-light;
}
.flex-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: stretch;
          align-items: stretch;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}


